import React from "react";
import Table from "components/ui/Table";
import { useQuery } from "react-query3";
import { CustomColumns, JobParameter } from "types";
import { useParams } from "react-router";

export default function JobParameterTable(): JSX.Element {
  const { id } = useParams()
  const { data, isLoading, isFetching } = useQuery<JobParameter[]>(
    `/job/${id}/parameter`,
    {
      enabled: !!id,
      refetchOnWindowFocus: false,
    }
  );

  const columns: CustomColumns<JobParameter>[] = [
    {
      title: "Name",
      key: "name",
      dataIndex: "name"
    },
    {
        title: "Value",
        key: "displayValue",
        dataIndex: "displayValue"
      },
]

  return (
    <Table
        title="Job Parameters"
        data={data}
        columns={columns}
        loading={isLoading || isFetching}
    />
  );
}
